<script>
	import {getRandomLightColor} from './utils/math.js'
	import {toast} from './utils/toast.js'
	import {download} from './utils/download.js'
	import {deps} from './maps/departments.js'
	import {dcs} from './maps/dcs.js'
	import {escGroup} from './maps/escalationGroups.js'
	import {ticketStatus} from './maps/ticketStatuses.js'
	import GridExample from './GridExample.svelte'

	const storage = window.localStorage
	const options = JSON.parse(storage.getItem('opts') || '{}')

	let escGroupKeys = Object.keys(escGroup)
	let depsKeys = Object.keys(deps)
	let dcsKeys = Object.keys(dcs)
	let statusKeys = Object.keys(ticketStatus)
	let departments = [...depsKeys.map(key => {
		return {
			key: key.substr(4,),
			val: key
		}
	})]
	let datacenters = [
		...dcsKeys.map(key => {
			return {
				key: key.substr(3,),
				val: key
			}
		})
	]

	let datacentersTickets = [...datacenters, {
		key: 'Global',
		val: null
	}]
	let statuses = [
		{
			key: 'All Statuses',
			val: null
		},
		...statusKeys.map(key => {
			return {
				key: key.substr(13,),
				val: key
			}
		})
	]
	let escGroups = [
		...escGroupKeys.map(key => {
			return {
				key: key.substr(9,),
				val: key
			}
		})
	]

	let uploadBoxClassesBase = 'upload-box'
	let uploadBoxClasses = uploadBoxClassesBase

	$: queues = JSON.parse(storage.getItem('config') || '[]')

	const updateLocation = ({detail}, i) => {
		queues[i].colStart = detail.colStart
		queues[i].colSpan = detail.colSpan
		queues[i].rowStart = detail.rowStart
		queues[i].rowSpan = detail.rowSpan
	}

	$: mapOtherQueues = (arr, ind) => {
		let newArr = [...arr]
		// newArr.splice(ind,ind)

		return newArr
	}

	const addNewQueue = () => {
		queues = [...queues, {
			colStart: 1,
			colSpan: 3,
			rowStart: 1,
			rowSpan: 1,
			url: [],
			title: "New Queue",
			freq: 1,
			handleYellow: false,
			customURL: false
		}]
	}

	const deleteQueue = i => {
		queues.splice(i,1)
		queues = [...queues]
	}

	function saveQueues() {
		storage.setItem('config', JSON.stringify(queues))
	}

	function downloadConfig() {
		download({queues, opts: options})
	}

	function dragenter(e) {
		e.stopPropagation();
		e.preventDefault();
		uploadBoxClasses = uploadBoxClassesBase + ' active'
	}

	function dragover(e) {
		e.stopPropagation();
		e.preventDefault();
		uploadBoxClasses = uploadBoxClassesBase + ' active'
	}

	function dragexit(e) {
		e.stopPropagation();
		e.preventDefault();
		uploadBoxClasses = uploadBoxClassesBase
	}

	function drop(e) {
		e.stopPropagation();
		e.preventDefault();
		
		uploadBoxClasses = uploadBoxClassesBase
		const dt = e.dataTransfer;
		const files = dt.files;

		const parentKeys = ['queues', 'opts']

		const allowedKeys = [
			'colStart', 'colSpan', 'rowStart', 'rowSpan', 'url', 'title', 'freq', 'handleYellow', 'api'
		]
		const reader = new FileReader();
		reader.onload = (e) => {
			// The file's text will be printed here
			try {
				const conf = JSON.parse(e.target.result, parentKeys)
				queues = JSON.parse(JSON.stringify(conf.queues), allowedKeys)
				storage.setItem('opts', JSON.stringify(conf.opts || {}))
				storage.setItem('config', JSON.stringify(queues || []))

				toast('Config loaded and saved', 4000, 'success')
			} catch (err) {
				console.error(err)
				toast('Invalid config file, see browser console', 8000, 'error')
			}
		}

		reader.readAsText(files[0])
	}
</script>

<div class="grid-container">
	<div class="grid">
		<div style="grid-column:1/span 6;grid-row:1/span 10" class="scroll-y">
			<div class="card fill-x">
				<div class="card-header">
					<p class="h3 lac">DTS Options</p>
				</div>
			</div>
			{#each queues as q,i}
				<div class="card fill-x">
					<div class="card-body">
						<p class="lar"><i class="material-icons circle red" on:click={() => deleteQueue(i)}>delete</i></p>
						<!-- Title -->
						<div class="form-group lac">
							<span class="control-label">Title</span>
							<input type="text" bind:value={q.title}>
						</div>

						<!-- URL -->
						<div class="form-group">
							<span class="control-label">URL</span>
							
							<!-- Custom url -->
							<select bind:value={q.customURL} on:change={() => {q.url = q.customURL ? '' : []}}>
								<option value={false}>Structured URL</option>
								<option value={true}>Custom URL</option>
							</select>
							{#if q.customURL}
								<input type="text" bind:value={q.url} placeholder="Enter URL">
							{:else}
								<!-- base -->
								<select bind:value={q.url[0]} on:change={() => {q.url.length = 1}}>
									<option value="ims">IMS</option>
									<option value="imsGov">IMS (US Gov)</option>
								</select>
								<!-- Queue Type -->
								<select bind:value={q.url[1]} on:change={() => {q.url.length = 2;if (q.url[1] === 'srvcTicket') {q.url=[...q.url, [], [], []];q.api=true}else{q.api=false}}}>
									<option value="srvcTxnByDC">Transactions</option>
									<option value="srvcAdvTicketSearch">Tickets</option>
									<option value="srvcTicket">Tickets (API)</option>
									<option value="srvcEscalations">Escalations</option>
								</select>
								<!-- Ticket queue -->
								{#if q.url[1] === 'srvcAdvTicketSearch'}
									<!-- Department -->
									<select bind:value={q.url[2]}>
										{#each departments as a}
											<option value={a.val}>{a.key}</option>
										{/each}
									</select>

									<!-- Datacenter -->
									<select bind:value={q.url[3]}>
										{#each datacentersTickets as a}
											<option value={a.val}>{a.key}</option>
										{/each}
									</select>

									<!-- Ticket status -->
									<select bind:value={q.url[4]}>
										{#each statuses as a}
											<option value={a.val}>{a.key}</option>
										{/each}
									</select>

									<!-- Yellow ticket handler -->
									<select bind:value={q.handleYellow}>
										<option value={false}>No yellow notification</option>
										<option value={true}>Yellow ticket notificiation</option>
									</select>
								
								<!-- Ticket API queue -->
								{:else if q.url[1] === 'srvcTicket'}
									<select multiple bind:value={q.url[2]}>
										{#each departments as a}
											<option value={a.val}>{a.key}</option>
										{/each}
									</select>
									<select multiple bind:value={q.url[3]}>
										{#each datacentersTickets as a}
											<option value={a.val}>{a.key}</option>
										{/each}
									</select>
									<select multiple bind:value={q.url[4]}>
										{#each statuses as a}
											<option value={a.val}>{a.key}</option>
										{/each}
										<option value="ticketStatus_Orphan">Orphan</option>
									</select>
									<select bind:value={q.url[5]}>
										<option value='autoclose_false'>Hide autoclose</option>
										<option value='autoclose_true'>Show only autoclose</option>
										<option value=''>Show all</option>
									</select>
									<!-- Yellow ticket handler -->
									<select bind:value={q.handleYellow}>
										<option value={false}>No yellow notification</option>
										<option value={true}>Yellow ticket notificiation</option>
									</select>
								{:else if q.url[1] === 'srvcTxnByDC'}
									<select bind:value={q.url[2]}>
										{#each datacenters as a}
											<option value={a.val}>{a.key}</option>
										{/each}
									</select>
								{:else if q.url[1] === 'srvcEscalations'}
									<select bind:value={q.url[2]}>
										{#each escGroups as a}
											<option value={a.val}>{a.key}</option>
										{/each}
									</select>
								{/if}
							{/if}
						</div>

						<!-- Interval -->
						<div class="form-group lac">
							<span class="control-label">Refresh multiplier</span>
							<input type="number" bind:value={q.freq}>
							<input type="text" disabled value="Every {q.freq * 30} sec">
						</div>

						<!-- Use API instead of IMS -->
						{#if q.url[1] === 'srvcTxnByDC'}
						<div class="form-group lac">
							<span class="control-label">Use Internal API instead of IMS</span>
							<select bind:value={q.api}>
								<option value={false}>IMS</option>
								<option value={true}>API</option>
							</select>
						</div>
						{/if}

						<!-- Dimensions -->
						<div class="form-group">
							<p class="h4 lac">Layout</p>
							<div class="form-wrapper">
								<div class="form-group">
									<span class="control-label">Column Start</span>
									<input type="text" bind:value={q.colStart}>
								</div>
								<div class="form-group">
									<span class="control-label">Column Span</span>
									<input type="text" bind:value={q.colSpan}>
								</div>
								<div class="form-group">
									<span class="control-label">Row Start</span>
									<input type="text" bind:value={q.rowStart}>
								</div>
								<div class="form-group">
									<span class="control-label">Row Span</span>
									<input type="text" bind:value={q.rowSpan}>
								</div>
							</div>
							<GridExample loc={q} on:newlocation={(ev) => updateLocation(ev, i)} others={mapOtherQueues(queues,i)}></GridExample>
						</div>
					</div>
				</div>
			{/each}

			<!-- Options card -->
			<div class="card fill-x">
				<div class="card-header"><span class="h3 lac">Options</span></div>
				<div class="card-body">
					<!-- Show Search -->
					<div class="form-group">
						<span class="control-label">Show Search</span>
						
						<select bind:value={options.showSearch} on:change={() => {storage.setItem('opts', JSON.stringify(options))}}>
							<option value={false}>False</option>
							<option value={true}>True</option>
						</select>
					</div>

					<!-- Dark mode -->
					<div class="form-group">
						<span class="control-label">Dark mode</span>
						
						<select bind:value={options.dark} on:change={() => {storage.setItem('opts', JSON.stringify(options))}}>
							<option value={false}>Disabled</option>
							<option value={true}>Active</option>
						</select>
					</div>

					<!-- Pretty notes -->
					<div class="form-group">
						<span class="control-label">Pretty notes</span>
						
						<select bind:value={options.prettyNotes} on:change={() => {storage.setItem('opts', JSON.stringify(options))}}>
							<option value={false}>Disabled</option>
							<option value={true}>Active</option>
						</select>
					</div>

					<!-- RMS/IPMI on BDS -->
					<div class="form-group">
						<span class="control-label">Show IPMI/RMS link on BDS Page</span>
						
						<select bind:value={options.showRMSOnBDS} on:change={() => {storage.setItem('opts', JSON.stringify(options))}}>
							<option value={false}>Disabled</option>
							<option value={true}>Active</option>
						</select>
					</div>

					<!-- IPMI details on BDS -->
					<div class="form-group">
						<span class="control-label">Retrieve IPMI credentials and show on BDS</span>
						
						<select bind:value={options.showIPMIOnBDS} on:change={() => {storage.setItem('opts', JSON.stringify(options))}}>
							<option value={false}>Disabled</option>
							<option value={true}>Active</option>
						</select>
					</div>

					<!-- Load all pages of BDS queue on BDS -->
					<div class="form-group">
						<span class="control-label">Load all pages of BDS queues on BDS page (single page BDS)</span>
						
						<select bind:value={options.singlePageBDSQueue} on:change={() => {storage.setItem('opts', JSON.stringify(options))}}>
							<option value={false}>Disabled</option>
							<option value={true}>Active</option>
						</select>
					</div>
					
					<div class="form-group">
						<a href="https://internal.applb.dal10.softlayer.local/v3.1/internal/json/" class="control-label" target="_blank">Fix API queues/services on Firefox (click <code>Advanced -> Accept the Risk and Continue</code>)</a>
						<p><code><strong>Note:</strong> Due to the way SoftLayer handles it's internal services (e.g. the SoftLayer API), you have to "Accept the risk" on the any of the API routes (linked above) so Dash can make calls to it.<br>The only reason you have to do this is that the SoftLayer Internal API has a SoftLayer signed SSL certificate.<br>If you use Chrome or any Chromium based browser, fixing this issue is a little more complex. Contact Jake for instructions.</code></p>
					</div>
				</div>
			</div>

			<!-- Add New Queue -->
			<div class="card fill-x">
				<div class="card-header">
					<button class="h3 lac" on:click={addNewQueue}>Add new Queue</button>
				</div>
			</div>

			<!-- Save Queues to localstorage -->
			<div class="card fill-x" >
				<div class="card-header">
					<button class="h3 lac" on:click={saveQueues}>Save Config</button>
				</div>
			</div>

			<!-- Download Config File -->
			<div class="card fill-x">
				<div class="card-header">
					<button class="h3 lac" on:click={downloadConfig}>Download Config</button>
				</div>
			</div>
			<!-- Load Config -->
			<div class="card fill-x">
				<div class="card-header lac">
					<span class="h3">Load Config</span>
				</div>
				<div class="card-body lac">
					<div class={uploadBoxClasses} on:dragenter={dragenter} on:dragover={dragover} on:drop={drop} on:dragleave={dragexit}>
						<p class="h4">Drag a config file over this box to load it</p>
					</div>
				</div>
			</div>
		</div>
	</div>
</div>